a.btn.btn-primary {
  &:hover {
    color: $brand-yellow;
  }
}

.button {
  @extend .btn;
}

.button--primary {
  @extend .btn-primary;
}
.button--success {
  @extend .btn-success;
}
.button--info {
  @extend .btn-info;
}
.button--danger {
  @extend .btn-danger;
}
.button--warning {
  @extend .btn-warning;
}

.button--small {
  @extend .btn-sm;
}

.btn-link-info {
  color: $brand-warning ;
}

.agora-header,
.view-display-id-home {
  .more-link {
    @extend .btn;
    @extend .btn-block;
    @extend .btn-yellow;
  }
}

.leaflet-container a.btn-link {
  @extend .btn-link;
}



