header.navbar {
  margin-bottom: 0;
}

.agora-header {
  .jumbotron {
    margin-bottom: 0;
  }

  .carousel-inner {
    height: 650px !important;
    overflow: hidden;
  }

}

h1, h2, header .navbar-brand {
  font-family: 'arc-light';
  text-transform: uppercase;
  small {
    font-family: $headings-font-family;
    text-transform: none;
  }

}

h1, h2, h3, h4, label {
  &.alt {
    font-family: 'arc';
    text-transform: uppercase;
  }
  &.alt-light {
    font-family: 'arc-light';
    text-transform: uppercase;
  }
}

header .navbar-brand {
  line-height: 1.5;
}

header.navbar ul.nav.navbar-nav.navbar-right {
  @media #{$tablet} {
    position: absolute;
    right: 15px;
  }
}

.view-header {
  & > ul.links {
    padding: 0;
    list-style: none;
    li a {
      @extend .btn;
      @extend .btn-sm;
      @extend .btn-primary;
    }
  }
}



