/**
 * Default Variables.
 *
 * Modify this file to provide default Bootstrap Framework variables. The
 * Bootstrap Framework will not override any variables defined here because it
 * uses the `!default` flag which will only set their default if not already
 * defined here.
 *
 * You can copy existing variables directly from the following file:
 * ./THEMENAME/bootstrap/assets/stylesheets/_variables.scss
 */

// Set the proper directory for the Bootstrap Glyphicon font.
$icon-font-path: '~bootstrap-sass/assets/fonts/bootstrap/';

// Font Awesome | see: http://fontawesome.io/get-started/
$fa-font-path:          '~font-awesome/fonts';

$web-font-path: "https://fonts.googleapis.com/css?family=Lato:300,400,400i,700&subset=latin-ext";

$font-size-base:          14px;
$font-weight-base:        300;

$font-size-h1:            34px;
$font-size-h2:            24px;
$font-size-h3:            20px;
$font-size-h4:            18px;
$font-size-h5:            16px;
$font-size-h6:            16px;

$jumbotron-heading-font-size:     48px;

$link-hover-decoration: none;

$navbar-border-radius: 0;

$dark-red: #A91833;
$red: #D81E41;
$purple: #6D1F80;

$gray-base:              #000 ;
$gray-darker:            lighten($gray-base, 13.5%) ;
$gray-dark:              #303030 ;;
$gray:                   #464545 ;
$gray-light:             #999 ;
$gray-lighter:           #EBEBEB ;

$brand-yellow:          transparentize(#FFFF00, 0.1) ;
$brand-green:           transparentize(#01E629, 0.3) ;
$brand-red:             transparentize(#FF4500, 0.3) ;
$brand-blue:            transparentize(#00BFFF, 0.2) ;

$brand-primary:         $gray-dark ;
$brand-success:         #18BC9C ;
$brand-info:            $brand-blue;
$brand-warning:         #F39C12 ;
$brand-danger:          $brand-red ;

// Reset inverted navbar basics
$navbar-inverse-bg:                         $gray-darker;
$navbar-inverse-border:                     transparent;

// Inverted navbar links
$navbar-inverse-link-color:                 #fff;
$navbar-inverse-link-hover-color:           $brand-yellow ;
$navbar-inverse-link-hover-bg:              transparent;
$navbar-inverse-link-active-color:          $brand-yellow;
$navbar-inverse-link-active-bg:             transparent;
$navbar-inverse-link-disabled-color:        #444;
$navbar-inverse-link-disabled-bg:           transparent;
$navbar-inverse-brand-hover-color:          $brand-yellow ;

$btn-primary-bg:                            $gray-darker;
$btn-primary-color:                         #fff;
$btn-primary-border:                        $gray-darker;

//** Width of horizontal description list titles
$dl-horizontal-offset:        36px;
//** Point at which .dl-horizontal becomes horizontal
$dl-horizontal-breakpoint:    $grid-float-breakpoint;

$carousel-control-color:                     $brand-yellow;
$carousel-control-width:                      15% !default;
$carousel-control-opacity:                    .5 !default;
$carousel-control-font-size:                  20px !default;

$carousel-indicator-active-bg:                $brand-yellow;
$carousel-indicator-border-color:             $brand-yellow;

$carousel-caption-color:                      $brand-yellow;



